.header-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    margin: 5px 40px;
    margin-bottom: 20px;
}

.header-text {
    flex: 1;
    font-family: var(--font-family);
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: space-between;
    text-align: left;
}

.header-text h1 {
    font-family: 700;
    line-height: 56px;
}

.header-text p {
    font-family: 300;
    line-height: 32px;
    color: #7D7987;
    ;
}

.header-text button {
    width: 150px;
    height: 50px;
    border-radius: 55px;
    background: #458FF6;
    border: none;
}

.header-image{
    flex: 1;
    width: 100%;
    height: 100%;
    margin-left: 10px;
}

@media screen and (max-width: 1050px) {
    .header-container {
        flex-direction: column;
    }
}