.footer-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    font-size: 18px;
    color: white;
    background: linear-gradient(183.41deg, #67C3F3 -8.57%, #5A98F2 82.96%);
    width: 100%;
    padding: 0;
    margin: 0;
}

.footer-logo {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.footer-logo button {
    background-color: white;
    border-radius: 20px;
    border: none;
    width: 30px;
    height: 32px;
}

.footer-logo h4 {
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: 700;
    line-height: 30px;
    margin-top: 35px;
}

.footer-text {
    display: flex;
    flex: 1;
    flex-direction: column;
    font-family: var(--font-family);
    padding-left: 30px;
    margin: 0;
}


.footer-content {
    display: flex;
    flex: 1.5;
    flex-direction: row;
    justify-content: start;
    align-items: start;
    font-family: var(--font-family);
}

.footer-content-links {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.footer-content-links li {
    margin-bottom: 10px;
    list-style: none;

}


@media screen and (max-width: 850px) {
    .footer-container {
        flex-direction: column;
        text-align: left;
    }

    .footer-text{
        flex:1;
    }

    .footer-content{
        flex: 1;
        
    }

}

@media screen and (max-width: 880px) {
    .footer-container {
        flex-direction: column;
        text-align: left;
        align-items: start;
        justify-content: start;
    }

    .footer-text{
        flex:1;
    }

    .footer-content{
        flex: 1;
        flex-direction: column;
    }

}