.testimony-container {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}

.testimony-content {
    background: linear-gradient(208.18deg, #67C3F3 9.05%, #5A98F2 76.74%);
    color: white;
    font-family: var(--font-family);
    Width: 100%;
    display: flex;
  flex-direction: column; /* Stack the content vertically */
  border-radius: 24px;
}

.testimony-content h1 {
    margin-top: 20px;
}

.testimony-content .bar {
    width: 50px;
    height: 3px;
    background-color: white;
    margin: auto;
    margin-bottom: 15px;
}

.testimony-text {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    margin-bottom: 30px;
}

.testimony-text-profile{
    flex: 1;
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
}

.testimony-text-profile-image{
    display: flex;
    justify-content: center;
    align-items: center;
}

.testimony-text-profile-content{
    margin-left: 10px;
    
}

.testimony-text-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-right: 50px;
}