.downapp-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    margin: 5px 40px;
    margin-bottom: 40px;
}

.downapp-text {
    flex: 1;
    font-family: var(--font-family);
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: space-between;
    text-align: left;
}

.downapp-text h1 {
    font-family: 700;
    line-height: 56px;
}

.downapp-text div {
    width: 56px;
    height: 2px;
    border-radius: 6px;
    background-color: black;
}

.downapp-text p {
    font-family: 300;
    line-height: 32px;
    color: #7D7987;
    ;
}

.downapp-text button {
    width: 150px;
    height: 50px;
    border-radius: 55px;
    background: transparent;
    border: 1.4px solid #458FF6;
    align-items: center;
}

.downapp-image{
    flex: 1;
    width: 100%;
    height: 100%;
    margin-left: 10px;
}

.bold-arrow {
    font-size: 1.2rem;
}

@media screen and (max-width: 1050px) {
    .downapp-container{
        flex-direction: column;
        margin: auto;
        margin-bottom: 20px;
    }

}

