.navbar-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 5px 50px;
    margin-bottom: 20px;
}

.navbar-logo {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.navbar-logo button {
    background-color: #458FF6;
    border-radius: 20px;
    border: none;
    width: 30px;
    height: 32px;
}

.navbar-logo h4 {
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: 700;
    line-height: 30px;
    margin: 0 0 0 10px;
    color: #233348;
    ;
}

.navbar-links {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.navbar-links p {
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 400;
    margin-right: 30px;
    cursor: pointer;
    color: #1F1534;
}

.navbar-links p:first-child {
    font-weight: 700;
}

.navbar-links p a {
    text-decoration: none;
}

.navbar-menu {
    display: none;
    position: relative;

}

.navbar-menu-container{
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: white;
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

@media screen and (max-width: 1050px) {
    .navbar-menu{
        display: flex;
        align-items: right;
    justify-content: right;
    margin-right: -50px;
    }

    .navbar-links {
        display: none;
    }
}