.services-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: var(--font-family);
    max-width: 100%;
    margin-bottom: 20px;
}

.services-text {
    text-align: center;
}

.services-text div {
    width: 56px;
    height: 2px;
    border-radius: 5px;
    background-color: black;
    margin: auto;
}



.services-card-container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    display: grid;
    grid-template-columns: auto auto auto;
    gap: 5px 10px;
    margin: auto;
}

.services-card {
    display: flex;
    justify-content: center;
    flex-direction: column;
    background-color: white;
    text-align: start;
    padding-left: 10px;
    border-radius: 20px;
    Width: 350px;
    Height: 354px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

.services-card-image {
    justify-content: start;
}

.services-card-text p {
    color: #7D7987;
}

.services-container button{
    width: 200px;
    height: 60px;
    border-radius: 55px;
    margin-top: 50px;

    border: 1.4px solid #458FF6;
    background-color: white;
}

.services-container button a {
    color: #458FF6;
    text-decoration: none;
}

@media screen and (max-width: 1120px) {
    .services-card-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    
        display: grid;
        grid-template-columns: auto auto;
        gap: 5px 10px;
        margin: auto;
    }
}

@media screen and (max-width: 850px) {
    .services-card-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    
        display: grid;
        grid-template-columns: auto;
        gap: 5px 10px;
        margin: auto;
    }
}