.article-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-family: var(--font-family);
    margin-bottom: 20px;
}

.article-container h1:first-child{
        text-align: center;
}

.article-container .bar {
    width: 60px;
    height: 3px;
    background-color: black;
    margin-bottom: 20px;
}

.article-cards-container {
    display: grid;
    grid-template-columns: auto auto auto;
    column-gap: 10px;
}

.article-card {
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items: start;
    text-align: left;
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
}

.article-card img {
    height: 100%;
    width: 100%;
}

.article-card h1,p,h4 {
    padding: 10px;
}

.article-card h4 {
    color: #458FF6;
}

.article-container button {
    width: 200px;
    height: 56px;
    border-radius: 55px;
    border: 1.4px;
    border: 1.4px solid #458FF6;
    background-color: white;
    margin-top: 20px;
}

.article-container button a {
    color: #458FF6;
}


@media screen and (max-width: 1120px) {
    .article-cards-container {
        
    
        grid-template-columns: auto;
        margin: auto;
        margin-bottom: 20px;
    }
    .article-card{
        flex-direction: row;
        margin-bottom: 20px;
    }

    .article-card img {
        height: auto;
        width: auto;
        margin: auto;
    }
}

@media screen and (max-width: 850px) {
    .article-cards-container {
    
        grid-template-columns: auto;
        margin: auto;
        margin-bottom: 20px;

    }

    .article-card{
        flex-direction: column;
        margin-bottom: 20px;
        align-items: center;
        justify-content: center;
        display: flex;
    }

    .article-card img {
        height: auto;
        width: auto;
        margin: auto;
    }
}