.blog-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    margin-bottom: 20px;
}

.blog-text {
    margin-left: 100px;
    flex: 1;
    font-family: var(--font-family);
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: space-between;
    text-align: left;
}

.blog-text h1 {
    font-family: 700;
    line-height: 56px;
}

.blog-text div {
    width: 56px;
    height: 2px;
    border-radius: 6px;
    background-color: black;
}

.blog-text p {
    font-family: 300;
    line-height: 32px;
    color: #7D7987;
    ;
}

.blog-text button {
    width: 150px;
    height: 50px;
    border-radius: 55px;
    background: transparent;
    border: 1.4px dashed #458FF6;

}

.blog-image{
    flex: 1;
    width: 100%;
    height: 100%;
    margin-left: 10px;
}

@media screen and (max-width: 1050px) {
    .blog-container{
        flex-direction: column;
    }

    .blog-text {
        margin: auto;
    }
}